import { basicDataBaseApiUrl } from '../../constants/baseConfig';

const WebApiConfig = {
    dic: {
        Base: basicDataBaseApiUrl + 'dic',//
        GroupList: basicDataBaseApiUrl + 'dictionarygroups/list',// post
        GroupListSave: basicDataBaseApiUrl + 'dictionarygroups',// post
        GroupListDelete: basicDataBaseApiUrl + 'dictionarygroups/',// 
        ParList: basicDataBaseApiUrl + 'dictionarydefines/',//  get
        ParListSave: basicDataBaseApiUrl + 'dictionarydefines',//
        ParListDelete: basicDataBaseApiUrl + 'dictionarydefines/delete',// 
        StartDicValue: basicDataBaseApiUrl + 'dictionarydefines/reuse',// put
    },
    area: {
        Base: basicDataBaseApiUrl + 'areadefines',//
        List: basicDataBaseApiUrl + 'areadefines/list',//1
        ChildList: basicDataBaseApiUrl + 'areadefines/',// 2,3
        Delete: basicDataBaseApiUrl + 'areadefines/delete',//
    },
}

export default WebApiConfig;


